.grid {
  position: relative;
  height: calc(100% - 50px);
  width: 90%;
  margin: 0 auto; }

#scrolling-wrapper {
  position: absolute;
  z-index: 10;
  bottom: 0;
  height: 100%;
  left: 10%;
  width: 80%;
  @media all and (min-width: 1300px) {
    justify-content: center; }
  #scroller {
    position: absolute;
    z-index: 1;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    transform: translateZ(0);
    user-select: none;
    padding-left: 50px;
    height: 90%;
    // @include phones
 } }    //    width: 2000px

.iScrollHorizontalScrollbar {
  left: 0 !important;
  right: 0 !important;
  &:before {
    content: '';
    background: $brand-orange;
    width: 100%;
    height: 2px;
    position: absolute;
    top: 50%;
    margin-top: -1px; } }


.iScrollIndicator {
  background: $brand-orange !important;
  border: 1px solid $brand-orange !important; }


.grid__item {
  position: relative;
  display: block;
  float: left;
  height: 50%;
  width: 360px;
  overflow: hidden;
  margin: 5px;
  transition: all 0.5s ease-in-out;
  box-sizing: border-box;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  &--green {
    background-color: rgba(green, 0.2); }
  &--red {
    background-color: rgba(red, 0.2); }
  @include phones {
    max-width: 320px; }
  &:hover {
    border-color: $brand-orange; }
  &:hover>a.grid__link {
    color: $brand-orange; }
  &:hover>a.grid__link>svg {
    fill: $brand-orange; } }

h2.grid__title {
  position: absolute;
  background-color: rgba($white, 0.7);
  top: 30%;
  z-index: 2;
  padding-left: 1em;
  width: 50%;
  padding: 1em; }

.grid__link {
  position: absolute;
  bottom: 5%;
  z-index: 2;
  right: 0;
  background-color: rgba(#000, 0.6);
  display: table;
  padding: 5px;
  text-decoration: none;
  transition: inherit;
  svg {
    display: inline-block;
    vertical-align: middle;
    fill: $white;
    transition: inherit; } }

.grid__linkname {
  display: inline-block;
  vertical-align: middle;
  text-decoration: underline;
  margin-right: 5px; }


.grid__image img {
  display: block;
  position: absolute;
  top: 2em;
  right: 1em;
  border-radius: 5px 0 0 0;
  z-index: 1; }

.drag-popup-icon {
  display: none;
  svg {
    fill: $white; }
  @media all and (max-width: 1300px) {
    display: block;
    position: absolute;
    left: 0;
    bottom: 20px; } }
